.list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.loadMoreButton {
  display: inline-flex;
  padding: 16px 32px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 200px;
  border: 1px solid rgba(71, 84, 103, 0.2);
  color: var(--Main, #101828);
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px; /* 150% */
  letter-spacing: -0.08px;
}

.loadMoreButton:hover {
  border-radius: 200px;
  border: 1px solid var(--Button, #e44848);
}
